"use client";

import { useState, useCallback } from "react";
import { useToast } from "@/shared/ui/use-toast";
import { TeamsService } from "../api/teams.service";
import { Team, CreateTeamDto, TeamMember } from "@/entities/team/model/types";

interface UseTeamsState {
  teams: Team[];
  currentTeam: Team | null;
  teamMembers: TeamMember[];
  isLoading: boolean;
  error: string | null;
}

export function useTeams() {
  const [state, setState] = useState<UseTeamsState>({
    teams: [],
    currentTeam: null,
    teamMembers: [],
    isLoading: false,
    error: null,
  });
  const { toast } = useToast();

  const createTeam = useCallback(
    async (name: string) => {
      try {
        setState((prev) => ({ ...prev, isLoading: true, error: null }));
        const team = await TeamsService.createTeam(name, token);
        setState((prev) => ({
          ...prev,
          teams: [...prev.teams, team],
          currentTeam: team,
          isLoading: false,
        }));

        toast({
          title: "Успешно!",
          description: "Команда создана",
        });

        return team;
      } catch (error) {
        const message =
          error instanceof Error
            ? error.message
            : "Ошибка при создании команды";
        setState((prev) => ({ ...prev, error: message, isLoading: false }));

        toast({
          variant: "destructive",
          title: "Ошибка!",
          description: message,
        });

        throw error;
      }
    },
    [toast, token]
  );

  const joinTeam = useCallback(
    async (code: string) => {
      try {
        setState((prev) => ({ ...prev, isLoading: true, error: null }));

        // Присоединяемся к команде по коду
        await TeamsService.joinTeam({ team_code: code });

        // После успешного присоединения получаем команду для отображения
        const team = await TeamsService.getTeamByCode(code);

        setState((prev) => ({
          ...prev,
          teams: [...prev.teams, team],
          currentTeam: team,
          isLoading: false,
        }));

        toast({
          title: "Успешно!",
          description: "Вы присоединились к команде",
        });

        return team;
      } catch (error) {
        let message = "Ошибка при присоединении к команде";

        if (error instanceof Error) {
          if (error.message.includes("Not Found")) {
            message = "Команда с таким кодом не найдена";
          } else {
            message = error.message;
          }
        }

        setState((prev) => ({ ...prev, error: message, isLoading: false }));

        toast({
          variant: "destructive",
          title: "Ошибка!",
          description: message,
        });

        throw error;
      }
    },
    [toast]
  );

  const fetchMyTeams = useCallback(async () => {
    try {
      setState((prev) => ({ ...prev, isLoading: true, error: null }));
      const teams = await TeamsService.getMyTeams();
      setState((prev) => ({ ...prev, teams, isLoading: false }));
      return teams;
    } catch (error) {
      const message =
        error instanceof Error
          ? error.message
          : "Ошибка при получении списка команд";
      setState((prev) => ({ ...prev, error: message, isLoading: false }));
      throw error;
    }
  }, []);

  const getTeamById = useCallback(async (teamId: number) => {
    try {
      setState((prev) => ({ ...prev, isLoading: true, error: null }));
      const team = await TeamsService.getTeamById(teamId);
      setState((prev) => ({ ...prev, currentTeam: team, isLoading: false }));
      return team;
    } catch (error) {
      const message =
        error instanceof Error ? error.message : "Ошибка при получении команды";
      setState((prev) => ({ ...prev, error: message, isLoading: false }));
      throw error;
    }
  }, []);

  const getTeamByCode = useCallback(async (code: string) => {
    try {
      setState((prev) => ({ ...prev, isLoading: true, error: null }));
      const team = await TeamsService.getTeamByCode(code);
      setState((prev) => ({ ...prev, currentTeam: team, isLoading: false }));
      return team;
    } catch (error) {
      const message =
        error instanceof Error ? error.message : "Ошибка при получении команды";
      setState((prev) => ({ ...prev, error: message, isLoading: false }));
      throw error;
    }
  }, []);

  const getTeamMembers = useCallback(async (teamId: number) => {
    try {
      setState((prev) => ({ ...prev, isLoading: true, error: null }));
      const members = await TeamsService.getTeamMembers(teamId);
      setState((prev) => ({ ...prev, teamMembers: members, isLoading: false }));
      return members;
    } catch (error) {
      const message =
        error instanceof Error
          ? error.message
          : "Ошибка при получении участников команды";
      setState((prev) => ({ ...prev, error: message, isLoading: false }));
      throw error;
    }
  }, []);

  const deleteTeam = useCallback(
    async (teamId: number) => {
      try {
        setState((prev) => ({ ...prev, isLoading: true, error: null }));

        await TeamsService.deleteTeam(teamId);

        setState((prev) => ({
          ...prev,
          teams: prev.teams.filter((team) => team.id !== teamId),
          currentTeam:
            prev.currentTeam?.id === teamId ? null : prev.currentTeam,
          isLoading: false,
        }));

        toast({
          title: "Успешно!",
          description: "Команда удалена",
        });
      } catch (error) {
        const message =
          error instanceof Error
            ? error.message
            : "Ошибка при удалении команды";
        setState((prev) => ({ ...prev, error: message, isLoading: false }));

        toast({
          variant: "destructive",
          title: "Ошибка!",
          description: message,
        });

        throw error;
      }
    },
    [toast]
  );

  return {
    ...state,
    createTeam,
    joinTeam,
    fetchMyTeams,
    getTeamById,
    getTeamByCode,
    getTeamMembers,
    deleteTeam,
  };
}
